import { Component, OnInit } from '@angular/core';
import { PrimeNGConfig } from 'primeng/api';
import { CourseService } from 'app/core/services/http/course/course.service';
import { ICourse } from 'app/core/services/http/course/course-model';

@Component({
  selector: 'app-create-course',
  templateUrl: './create-course.component.html',
  styleUrls: ['./create-course.component.css']
})
export class CreateCourseComponent implements OnInit {

  courses: ICourse[];

  constructor(private courseService: CourseService, private primengConfig: PrimeNGConfig) { }

  ngOnInit(): void {
    this.primengConfig.ripple = true;
    this.getCourse();
  }

  private getCourse() {
    this.courseService
      .getCourse()
      .subscribe((data) => (this.courses = data));
  }
}
